              console.log(err)
            })
          // memfilter hanya dua saja yang digunakan
            console.log(result[0]);
            for (var i = 0; i <= 2 ; i++) {
                // mengisi data.data dengan push result
                data.data.push(result[i])
            }
            return {...toRefs(data)};

